.hero-section {
  background-image: url(../../assets/images/Venison\ Kofta\ -\ Garlic\ Paprika.jpg);
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  margin-top: -5.6em;
  position: relative;
}

.hero-section::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0; left: 0;
  background: rgba(0, 0, 0, 0.1);
  pointer-events: none;
  transition: background 0.2s ease-out;
  z-index: 0;
}

.hero-section {
  position: relative;
  overflow: hidden;
}

.hero-overlay {
  position: absolute;
  top: 0; left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  pointer-events: none;
  transition: background 0.2s ease-out;
  z-index: 0;
}

.hs-greeting {
  margin: 6em 2em;
  max-width: 280px;
  display: flex;
  flex-direction: column;
  gap: 1.6em;
  z-index: 1;
}

.hs-greeting h1 {
  color: var(--white-100);
  font-weight: 300;
  font-size: 1.5rem;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
}

.hsg-search-meats-btn {
  width: 200px;
}

@media (min-width: 598px) {
    .hero-section {
        justify-content: flex-end;
        align-items: center;
    }

    .hs-greeting {
        margin-right: 2em;
    }
}

@media (min-width: 892px) {
    .hs-greeting {
        margin-right: 4em;
    }
}

@media (min-width: 1200px) {
    .hs-greeting {
        margin-right: 12em;
    }
}

@media (min-width: 1400px) {
    .hs-greeting {
        margin-right: 20em;
        transform: scale(1.1);
    }
}

/* === Recipe Section - Professional Styling === */
.recipe-section {
    padding: 5em 2em;
    background-color: var(--black-90);
    text-align: center;
}

.r-title {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 12px;
    margin-bottom: 4em;
    position: relative;
}

.r-title::after {
    content: '';
    position: absolute;
    bottom: -1.2em;
    width: 80px;
    height: 3px;
    background-color: var(--primary);
    border-radius: 6px;
    left: 50%;
    transform: translateX(-50%);
}

.r-title h2 {
    color: var(--white-100);
    font-weight: 600;
    font-size: 2rem;
    text-transform: none;
    letter-spacing: 0.5px;
}

.r-btn {
    display: inline-block;
    margin-top: 3em;
    padding: 0.9em 2em;
    font-size: 1rem;
    font-weight: 500;
    color: var(--white-100);
    background-color: var(--primary);
    border-radius: 8px;
    transition: background-color 0.3s ease;
    text-decoration: none;
}

.r-btn:hover {
    background-color: var(--primary-dark);
}


