.bestsellers-section {
  padding: 4em 1em;
  position: relative;
  background-image:
    linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url('../../assets/images/british-landscape.jpeg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.bs-title {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    margin-bottom: 6em;
}

.award-icon {
    width: 42px;
    fill: var(--white-50);
}

.bs-title h2 {
    color: var(--white-50);
    font-weight: 300;
    text-transform: uppercase;
}

.bs-btn {
    display: block;
    margin: auto;
    margin-top: 4.5em;
    max-width: 260px;
}

/* Other Products Section */
.other-products-section {
    padding: 4em 1em;
    background-color: var(--black-75);
}

.ops-title {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    margin-bottom: 4em;
}

.add-cart-home-icon {
    width: 38px;
    fill: var(--white-50);
}

.ops-title h2 {
    color: var(--white-50);
    font-weight: 300;
    text-transform: uppercase;
}

.ops-products {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 3.8em;
    margin-bottom: 5em;
}

.op-item {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.6em;
    width: 90%;
}

.op-item img {
    width: 50%;
    max-width: 340px;
}

.op-item-info {
    display: flex;
    flex-direction: column;
    gap: 0.4em;
}

.op-item-info h3 {
    color: var(--white-50);
    font-weight: 300;
    text-transform: uppercase;
    font-size: 1rem;
}

.op-item-info span {
    color: var(--white-25);
    font-weight: 300;
    text-transform: uppercase;
    font-size: 0.8rem;
}

.op-item:nth-of-type(2) {
    flex-direction: row-reverse;
}

.op-item:nth-of-type(2) .op-item-info {
    align-items: flex-end;
}

.ops-btn {
    width: 200px;
    margin-left: 60px;
}

@media (min-width: 598px) {
    .ops-products {
        gap: 6em;
        margin-bottom: 6em;
    }

    .ops-title {
        margin-bottom: 6em;
    }
}

@media (min-width: 698px) {
    .op-item {
        gap: 4em;
    }

    .op-item-info {
        transform: scale(1.1);
    } 
}

@media (min-width: 992px) {
    .op-item:nth-of-type(1),
    .op-item:nth-of-type(3) {
        transform: translateX(-8em);
    }

    .op-item:nth-of-type(2) {
        transform: translateX(8em);
    }
}

@media (min-width: 1200px) {
    .op-item:nth-of-type(1),
    .op-item:nth-of-type(3) {
        transform: translateX(-12em);
    }

    .op-item:nth-of-type(2) {
        transform: translateX(12em);
    }

    .ops-products {
        margin-bottom: 8em;
    }
}

/* About Us Section */
.about-us-section {
  padding: 6em 1em;
}

.aus-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2em;
  width: 90%;
  max-width: 900px;
  margin: auto;
  align-items: center;
}

.about-us-section img {
  width: 100%;
  object-fit: cover;
  border-radius: 6px;
}

.aus-info {
  color: var(--white-50);
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.aus-info h2 {
  font-weight: 300;
  text-transform: uppercase;
  font-size: 1.6rem;
}

.aus-info p {
  font-size: 0.9rem;
  text-align: start;
}

.aus-btn {
  display: block;
  max-width: 160px;
  margin-top: 1em;
}

.bestsellers-section {
  padding: 4em 1em;
  position: relative;
}

.bs-title {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-bottom: 4em;
}

.award-icon {
  width: 42px;
  fill: var(--white-50);
}

.bs-title h2 {
  color: var(--white-50);
  font-weight: 300;
  text-transform: uppercase;
}

.bs-btn {
  display: block;
  margin: 4em auto 0 auto;
  max-width: 260px;
}

.home-loading {
  position: fixed;
  width: 100%;
  height: 105vh;
  top: 0;
  left: 0;
  z-index: 1000;
  background: radial-gradient(circle, rgba(32,32,32,1) 0%, rgba(8,8,8,1) 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.5s ease-out;
}

.home-loading-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2em;
}

.home-loading-content img {
  width: 180px;
}

.home-loading-content span {
  font-size: 1.2rem;
  color: #646464;
}

@media (min-width: 598px) {
  .hero-section {
    justify-content: flex-end;
    align-items: center;
  }

  .hs-greeting {
    margin-right: 2em;
  }

  .aus-grid {
    grid-template-columns: 25% 75%;
  }
}

@media (min-width: 1200px) {
  .hs-greeting {
    margin-right: 12em;
  }
}

@media (min-width: 1400px) {
  .hs-greeting {
    margin-right: 20em;
    transform: scale(1.1);
  }
}

@media (max-width: 767px) {
  .hero-section {
    background-attachment: initial;
  }
}

/* Achievements Section (Inverse About Us Style) */
.achievements-section {
  padding: 6em 1em;
  background-color: var(--black-85);
}

.achievements-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2em;
  width: 90%;
  max-width: 1100px;
  margin: auto;
  align-items: center;
}

.achievements-grid img {
  width: 100%;
  border-radius: 8px;
}

.achievements-info {
  color: var(--white-50);
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.achievements-info h2 {
  font-weight: 300;
  text-transform: uppercase;
  font-size: 1.6rem;
}

.achievements-info p {
  font-size: 0.9rem;
  text-align: start;
}

.achievements-btn {
  max-width: 160px;
  margin-top: 1em;
  background-color: var(--primary);
  color: var(--white-100);
  padding: 0.8em 1.4em;
  border-radius: 6px;
  text-decoration: none;
  text-align: center;
  transition: background-color 0.3s ease;
}

.achievements-btn:hover {
  background-color: var(--primary-dark);
}

/* Awards image container */
.awards-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

/* Awards image styling */
.awards-image {
  max-width: 90%;
  max-height: 300px;
  object-fit: contain;
  border-radius: 8px;
}

@media (min-width: 768px) {
  .achievements-grid {
    grid-template-columns: 1fr 1fr;
  }

  /* Reverse the order so image is on right, text is on left */
  .achievements-grid {
    direction: rtl;
  }

  .achievements-grid > * {
    direction: ltr;
  }

  /* Adjust image size on desktop */
  .awards-image {
    max-width: 85%;
    max-height: 350px;
  }
  
  /* Align to top to prevent stretching */
  .achievements-grid {
    align-items: flex-start;
  }
}